.calculator {
	width: 484px;
	background: lightgray;
}

.card-list {
	display: flex;
	flex-wrap: wrap;
	width: 420px;
}

.card {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 40px;
	border: 1px solid black;
	margin: 2px;

	cursor: pointer;

	color: white;
	font-size: 24px;
}

.card__suite-c {
	background-color: lightgreen;
}

.card__suite-h {
	background-color: lightcoral;
}

.card__suite-d {
	background-color: lightblue;
}

.card__suite-s {
	background-color: black;
}

.card__empty {
	background-color: lightgray;
}

.card__disabled {
	opacity: 0.2;
}

.board, .player {
	display: flex;
}

.spectre {
	display: flex;
}


.hand {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 40px;
	border: 1px solid black;
	margin: 2px;

	opacity: 0.6;

	cursor: pointer;
	font-size: 20px;
}

.hand:hover {
	opacity: 1;
}

.hand__pair {
	background: grey;
}

.hand__suited {
	background: yellow;
}

.hand__offsuite {
	background: lightblue;
}

.hand__selected {
	opacity: 1;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.show_spectre {
	display: block;
	height: 40px;
}

.win_percent {
	float: right;
	margin-right: 300px;
}